.view a{display:inline-block; vertical-align:middle; height:36px; line-height:36px; padding:0 30px; font-size:16px; text-decoration:none; color:#383838}
.view a.is-active{color:white; background:#f56541}
.wine{border-radius:10px; margin:17px 0 20px 0; background-image:-moz-linear-gradient(#fff,#e6e7e8); background-image:-webkit-linear-gradient(#fff,#e6e7e8); background-image:linear-gradient(#fff,#e6e7e8); box-shadow:0 5px 14px rgba(0,0,0,0.4); *zoom:1; padding:13px 0; height:224px -13px -13px; font-size:16px; color:#383838; position:relative}
.wine:after{content:""; display:table; clear:both}
@media only screen and (max-width:1400px){.wine{margin-right:15px}
}
.wine .wine__info{position:absolute; background:url('../img/icons-s51a623a2b8.png') 0 -228px no-repeat; width:36px; height:37px; font:0/0 a; text-shadow:none; color:transparent; top:-7px; right:49px; display:none}
.wine .wine__check{position:absolute; top:-7px; right:-5px; background:url('../img/icons-s51a623a2b8.png') 0 0 no-repeat; width:36px; height:36px}
.wine.is-selected{background:#f9e4de}
.wine.is-selected .wine__check{background:url('../img/icons-s51a623a2b8.png') 0 -46px no-repeat; width:36px; height:36px}
.wine.is-selected .wine__info{display:block}
.wine p{margin-bottom:1em}
.wine .wine__pic{float:left; width:215px; text-align:center}
.wine .wine__text{width:433px; float:left; padding-top:11px}
.wine .wine__text h2{font-size:30px; font-weight:normal; color:#b00a4e; line-height:1.1; margin-bottom:15px}
.wine .wine__from{float:left; width:117px; padding-right:100px; padding-top:11px}
.wine .wine__type{width:192px; padding-right:100px; float:left; padding-top:11px}
.wine .wine__price{background:url('../img/icons-s51a623a2b8.png') 0 -760px no-repeat; width:146px; height:55px; position:absolute; right:113px; bottom:30px; color:white; font-size:30px; font-weight:normal; padding-left:15px; line-height:60px; -moz-box-sizing:border-box; -webkit-box-sizing:border-box; box-sizing:border-box}
.wine .wine__color{*zoom:1; margin-bottom:1em}
.wine .wine__color:after{content:""; display:table; clear:both}
.wine .wine__color i{float:left; margin-right:17px; position:relative; top:3px}
.wine .wine__color i.color1{background:url('../img/icons-s51a623a2b8.png') 0 -184px no-repeat; width:14px; height:34px}
.wine .wine__color i.color2{background:url('../img/icons-s51a623a2b8.png') 0 -825px no-repeat; width:16px; height:34px}
.wine .wine__color i.color3{background:url('../img/icons-s51a623a2b8.png') 0 -598px no-repeat; width:14px; height:34px}
.wine .wine__color i.color4{background:url('../img/icons-s51a623a2b8.png') 0 -1030px no-repeat; width:11px; height:38px}